import { MockServerOpts } from "sockapi/server-config";
import { SavedHuntBackendState } from "sockapi/scope-specs/state-download-spec";
import {
  SharedWorkerReq,
  SharedWorkerReqType,
  SharedWorkerRespType,
} from "sock-server/server-interface/SharedWorkerInterface";

class SharedWorkerPortController {
  port: MessagePort;
  handleMsg?: (msg: string) => void;
  removeClient?: () => void;
  isClosed: boolean;

  constructor(port: MessagePort) {
    this.port = port;
    this.isClosed = false;
  }

  init() {
    this.port.onmessage = (msg) => {
      this.handlePortMsg(msg).catch(console.error);
    };
    this.port.start();
  }

  async handleInit(
    mockServerOpts: MockServerOpts,
    connUid?: string,
    resetServer?: { initState?: SavedHuntBackendState }
  ) {
    if (import.meta.env.MODE !== "production") {
      const makeMockServer = (await import("makeMockServer")).default;

      if (resetServer !== undefined) {
        // Resetting the server will cause the old one to disconnect us,
        // resulting in another INIT call. We'll rely on that INIT call
        // to set up the connection.
        await makeMockServer(mockServerOpts, resetServer);
        return;
      }

      // This creates a mock server singleton, or returns the existing one.
      const sockServer = await makeMockServer(mockServerOpts);

      const clientInterface = sockServer.addClient(
        {
          send: (msg) => {
            this.port.postMessage({
              type: SharedWorkerRespType.RESP,
              msg,
            });
          },
          close: () => {
            this.port.postMessage({
              type: SharedWorkerRespType.CLOSE,
            });
          },
        },
        connUid
      );

      this.handleMsg = clientInterface.handleMsg;
      this.removeClient = clientInterface.removeClient;

      this.port.postMessage({
        type: SharedWorkerRespType.INIT_ACK,
      });
    }
  }

  async handlePortMsg({ data }: MessageEvent<SharedWorkerReq>) {
    if (this.isClosed) return;

    switch (data.type) {
      case SharedWorkerReqType.INIT: {
        await this.handleInit(
          data.mockServerOpts,
          data.connUid,
          data.resetServer
        );
        break;
      }
      case SharedWorkerReqType.HANDLE_MSG: {
        this.handleMsg?.(data.msg);
        break;
      }
      case SharedWorkerReqType.REMOVE_CLIENT: {
        this.removeClient?.();
        break;
      }
    }
  }
}

// @ts-expect-error: global types are hard
onconnect = (e: MessageEvent) => {
  const portController = new SharedWorkerPortController(e.ports[0]);
  portController.init();
};
